import precincts from '../data/precincts.json';
import { Mercator } from '@visx/geo';

const MyMap = () => {
  const width = 800;
  const height = 500;
  const centerX = width / 2;
  const centerY = height / 2;
  const scale = 46000;
  const center = [-74.0438790753777, 40.6901959259204];

  return (
    <svg width={width} height={height}>
      
      <Mercator
        data={precincts.features}
        translate={[centerX, centerY]}
        scale={scale}
        center={center}
      >
        {({features}) => 
          features.map(({feature, path, projection}, i) => {
            return (
              <path 
                key={`map-feature-${i}`}
                d={path || ''}
                fill='#aaa'
                stroke='#555'
                strokeWidth={0.5}
              />
            )
          })
      }
      </Mercator>
    </svg>
  );
}

export default MyMap;